body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
  "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
  sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
  monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #09d3ac;
}

.Login {
  max-width: 500px;
  margin: 20px;
  border: 1px solid #d1d1d1;
  padding: 20px;
  border-radius: 5px;
}

.popup{
  position: absolute;
  width: 400px;
  border: 1px solid;
  padding: 20px;
  left: calc(50% - 200px);
  top: 50px;
  background: wheat;
}

.top-popup{
  position: absolute;
  width: 350px;
  border: 1px solid;
  padding: 20px;
  left: calc(50% - 200px);
  top: 60px;
  background: wheat;
}